import { Component, h, Host, Prop, Element, State } from '@stencil/core';
import ValidateInput from '../../input/validate';

@Component({
    tag: 'ks-page-account-password',
    styleUrl: 'password.css'
})
export class PageAccountPassword {
    @Element() root: HTMLElement;
    
    @Prop() heading: string = "Zmiana Hasła";
    @Prop() customerId: string;

    @Prop() oldPasswordApi: string = "res/true?haslo_stare=";
    @Prop() oldPasswordMessage: string = "Wpisane hasło nie jest poprawne";
    @Prop() changeApi: string;
    
    @Prop() accountLink: string;

    @Prop() success_heading: string = "Zmieniono hasło";
    @Prop() success_message: string = "Hasło zostało prawidłowo zmienione, za chwilę nastąpi przekierowanie na panel klienta.";

    @Prop() failure_heading: string = "Hasło nie zostało zmienione";
    @Prop() failure_message: string = "Spróbuj wprowadzić dane ponownie, jeżeli problem pojawi się ponownie skontaktuj się z obsługą sklepu.";
    
    @State() loading: boolean = false;
    @State() notification_heading: string;
    @State() notification_message: string;

	render() {
		return <Host>
            <section class="container">
                <h1>{this.heading}</h1>

                <form id="change" enctype="application/x-www-form-urlencoded" onSubmit={(event)=>this.submit_change(event)}>
                    <ks-input-dynamic api={this.oldPasswordApi} message={this.oldPasswordMessage}>
                        <ks-input-text password required name='haslo_stare' label="Stare hasło" icon="key" />
                    </ks-input-dynamic>
                    <ks-input-text password required name='nowe_haslo' label="Nowe hasło" icon="key" max={128} passwordCharacters />
                    <ks-input-text password required name='haslo_powtorz' label="Powtórz nowe hasło" icon="key" sameAs="nowe_haslo" />

                    <input type="hidden" name="id" value={this.customerId} />
                    <input type="hidden" name="akcja" value="zapisz" />

                    <div class="buttons">
                        <ks-button rounded submit name="Zmień hasło" loading={this.loading} />
                        <ks-button rounded border link={this.accountLink} name="Wróć do panelu klienta" loading={this.loading} />
                    </div>
                </form>

                <ks-dialog id="notification-dialog" dark>
                    <section>
                        <h1>{this.notification_heading}</h1>
                        <p>{this.notification_message}</p>

                        <ks-button rounded onClick={()=>this.hide_notification()} name="Wróć do logowania" />
                    </section>
                </ks-dialog>

                <ks-overlay dark id="loading-overlay" close={false}>
                    <div class="success">
                        <h1>{this.success_heading}</h1>
                        <p>{this.success_message}</p>
                        <div class="loader">
                            <ks-loader large />
                        </div>
                    </div>
                </ks-overlay>
            </section>
        </Host>;
	}

    redirect_to_account() {
        const dialog = this.root.querySelector("#loading-overlay") as any;
        dialog.show();

        setTimeout(() => {
            window.location.href = this.accountLink;
        }, 1000);
    }

    show_notification(heading: string, message: string) {
        this.notification_heading = heading;
        this.notification_message = message;

        const dialog = this.root.querySelector("#notification-dialog") as any;
        dialog.show();
    }

    hide_notification() {
        const dialog = this.root.querySelector("#notification-dialog") as any;
        dialog.hide();
    }

    async submit_change(event: Event) {
        event.preventDefault();
        this.loading = true;

        const form = this.root.querySelector("#change") as HTMLFormElement;

        if(!await ValidateInput(form)) {
            this.loading = false;
            return;
        }

        const filteredParams = new URLSearchParams();

        new FormData(form).forEach((value, key) => {
            filteredParams.append(key, value.toString());
        })

        const formData = new FormData();
        formData.append("data", filteredParams.toString());

        var headers = new Headers();
        headers.append('pragma', 'no-cache');
        headers.append('cache-control', 'no-cache');

        await fetch(this.changeApi, {
            body: formData,
            method: 'POST',
            headers: headers,
            credentials: "same-origin"
        })
        .then(response => {
            if(!response.ok) throw response.status;
            return response.text();
        })
        .then(response => {
            if(response.includes("Twoje hasło zostało zmienione"))
                this.redirect_to_account();
            else this.show_notification(this.failure_heading, this.failure_message);
        })
        .catch(error => {
            console.log(error);
            let message = error;

            if(!navigator.onLine)
                message = "Brak połączenia z internetem.";

            else if(Number.isInteger(error))
                message = `Błąd połączenia - ${error}`;

            this.show_notification(this.failure_heading, message);
        });

        this.loading = false;
    }
}
